body {
  font-family: sans-serif;
  background-color: rgb(12, 12, 23);
  color: rgb(255, 165, 0);
}
#startScreen {
  background-color: rgb(12, 12, 23);
  position: absolute;

  width: 810px;
  height: 610px;
  margin: 0 auto;
}
#startText {
  color: rgb(255, 165, 0);
  top: 85%;
  text-align: center;
  position: absolute;
  margin-top: -25px;
  width: 100%;
  height: 100%;

  animation-name: textFlash;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.Title {
  color: rgb(238, 255, 0);
  top: 45%;
  text-align: center;
  position: absolute;
  margin-top: -25px;
  transform: perspective(60px) scaleY(3) rotateX(-20deg);
  filter: drop-shadow(0px 2px 0px rgb(219, 146, 9))
    drop-shadow(0px 2px 0px rgb(185, 123, 8))
    drop-shadow(0px 2px 0px rgb(141, 94, 6))
    drop-shadow(0px 2px 0px rgb(119, 79, 5))
    drop-shadow(0px 2px 0px rgb(65, 42, 0));
  margin-left: 150px;
  margin-right: 150px;
}

#score {
  margin-right: 100px;
}
#game {
  border-style: solid;
  border-radius: 4px;
  border-color: rgb(255, 165, 0);
}
#Score {
  color: rgb(255, 165, 0);
  text-align: left;
}
#Audio {
  color: rgb(255, 165, 0);
  float: right;
  text-align: right;
}
@keyframes textFlash {
  from {
    color: rgb(255, 165, 12);
    filter: blur(0px) drop-shadow(0px 0px 5px rgb(255, 208, 119));
  }
  to {
    color: rgb(255, 255, 12);
    filter: blur(0.5px) drop-shadow(0px 0px 25px rgb(255, 165, 0));
  }
}
